import * as React from 'react';

import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Container from '@mui/material/Container';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import { Button } from '@mui/material';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';

import { Link } from 'react-router-dom';

const Products = () => {
    return (
        <Container maxWidth="xl">
            
            <Box>
                <Box sx={{ bgcolor: '#ffece5', pt: 3, pl: 3, pb: 3, display: 'flex', flexWrap: 'wrap'}}>
                    <Card sx={{ maxWidth: 345, m:'auto' }}>
                        <CardHeader title="Torta merengon" sx={{textAlign: 'center'}} />

                        <CardMedia component="img" image="/products/product2.png" alt="qchimbitaproduct" sx={{maxHeight: 340, minHeight: 340}}/>
                        
                        <CardContent>
                            <Typography variant="body2" color="text.secondary">
                            *¡Para endulzar su día las clásicas fresas con crema con el toque mágico Q'Chimbita!* Postre con fruta de tu elección Kiwi, fresa, melocotón, mango, maracuyá o banano, con crema de la casa y salsa de tu elección. 🍌🍓🍑🥭🥝
                            </Typography>
                        </CardContent>
                        <CardActions disableSpacing>
                            <Typography variant="body" color="text.primary" sx={{fontWeight: 'bold'}}>
                                $7.000
                            </Typography>

                            <IconButton aria-label="add to favorites" sx={{ml:'auto'}}>
                                <AddShoppingCartIcon />
                            </IconButton>
                        </CardActions>
                    </Card>

                    <Card sx={{ maxWidth: 345, m:'auto' }}>
                        <CardHeader title="Merengon doble durazno" sx={{textAlign: 'center'}} />

                        <CardMedia component="img" image="/products/product3.png" alt="qchimbitaproduct" sx={{maxHeight: 340}}/>

                        <CardContent>
                            <Typography variant="body2" color="text.secondary">
                                Delicioso Merengón de Durazno con doble capa de crema de la casa, doble capa de merengue y salsa de tu elección. 🤤                        
                            </Typography>
                        </CardContent>
                        <CardActions disableSpacing>
                            <Typography variant="body" color="text.primary" sx={{fontWeight: 'bold'}}>
                                $11.000
                            </Typography>

                            <IconButton aria-label="add to favorites" sx={{ml:'auto'}}>
                                <AddShoppingCartIcon />
                            </IconButton>
                        </CardActions>
                    </Card>

                    <Card sx={{ maxWidth: 345, m:'auto' }}  >
                        <CardHeader title="Torta merengon" sx={{textAlign: 'center'}} />

                        <CardMedia component="img" image="/products/product4.png" alt="qchimbitaproduct" sx={{maxHeight: 340}}/>

                        <CardContent>
                            <Typography variant="body2" color="text.secondary">
                                Torta de 1000 gramos acompañada de Capa de merengue, Mora, Guanábana, Fresa, Kiwi, Durazno, Crema de la casa y Salsa de tu elección.  🎂
                            </Typography>
                        </CardContent>
                        <CardActions disableSpacing>
                            <Typography variant="body" color="text.primary" sx={{fontWeight: 'bold'}}>
                                $35.000
                            </Typography>

                            <IconButton aria-label="add to favorites" sx={{ml:'auto'}}>
                                <AddShoppingCartIcon />
                            </IconButton>
                        </CardActions>
                    </Card>
                </Box>       

                <Box sx={{ bgcolor: '#ffece5', pt: 3, pl: 3, pb: 3, display: 'flex', flexWrap: 'wrap'}}>
                    <Button sx={{ m: 'auto', bgcolor: '#EE9B92', color: 'white'}}> <Link to="/productos" style={{ textDecoration: 'none', border: 'none', color: 'white' }}> Ver productos </Link></Button>
                </Box>         
            </Box>

        </Container>
    );
}

export default Products;